.component-button-content {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.demo-button {
  padding: 12px 28px;
  background: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 2px 4px rgba(74, 144, 226, 0.3);
}

.demo-button:hover {
  background: #357abd;
  box-shadow: 0 4px 8px rgba(74, 144, 226, 0.4);
  transform: translateY(-1px);
}
